.example {
  background-color: #eaeaea;
}

.example__losung {
  font-size: 36px;
  line-height: 40px;
  margin-top: 78px;
  margin-bottom: 40px;
}

.example__text {
  font-family: "Lato", sans-serif;
  color: #444444;
  font-size: 14px;
  line-height: 18px;
}

.example__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.example__item {
  font-size: 24px;
  line-height: 24px;
  margin-top: 25px;
  margin-bottom: 9px;
}

.example__desc {
  font-family: "Lato", sans-serif;
  font-size: 12px;
  line-height: 12px;
  color: #444444;
  margin-bottom: 20px;
}

.example__description {
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
}
