  color: #000000;
  margin-top: 50px;
  margin-bottom: 49px;
}

.complate__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.comlate__name {
  font-size: 16px;
  line-height: 16px;
  color: #222222;
  margin-bottom: 13px;
  text-transform: uppercase;
}

.complate__description {
  font-family: "Lato", sans-serif;
  font-size: 14px;
  line-height: 14px;
  color: #444444;
  margin-bottom: 15px;
